import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import FloatingActionButtonCard from './FloatingActionButtonCard';
import {styled} from '@mui/system';

const StyledModal = styled(Modal)({
    width: '20vw',
    height: 'auto',
    position: 'fixed',
    top: 'auto',
    bottom: '20px',
    right: '20px',
    left: 'auto',
})

export default function FloatingActionButtonModal(props : any){
    return (
        <div>
            <StyledModal
                color='white'
                aria-label='chat'
                open={props.open}
                onClose={() => props.handleClose()}
                closeAfterTransition
            >
                <FloatingActionButtonCard />
            </StyledModal>
        </div>
    );
}