import * as React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import SimpleImageSlider from 'react-simple-image-slider';
import VizSensor from 'react-visibility-sensor';
import { Typography, Fade } from '@mui/material';

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'relative',
        backgroundColor: 'black',
        height: '80vh',
        width: '80wh',
        justifyContent: 'center',
        alignContent: 'center',
    },
    typo: {
        position: 'absolute',
    },
    container: {
        cursor: 'pointer',
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -40%)'
    },
}));

export default function BuildYourBoxster (){
    const classes = useStyles();
    const images = [
        {url: 'img/boxster1.jpg'},
        {url: 'img/boxster2.jpg'},
        {url: 'img/boxster3.jpg'},
        {url: 'img/boxster4.jpg'},
        {url: 'img/boxster5.jpg'},
    ];

    const handleOnClick = () => {
        window.open("https://www.porsche.com/korea/ko/modelstart/all/?modelrange=718");
    }
    
    return (
        <div className={classes.root}>
            <div className={classes.typo}>
                <Typography
                    style={{
                        fontFamily: 'Blacksword',
                        fontSize: '4vw', 
                        color: 'white',
                        transform: 'translate(60%, 10%)'
                    }}    
                >
                    Build Your Own Boxster
                </Typography>
            </div>
                <div className={classes.container}>
                            <SimpleImageSlider
                                navStyle={1}
                                showBullets={false}
                                autoPlay={true}
                                slideDuration={1}
                                navSize={50}
                                navMargin={30}
                                loop={true}
                                width={960}
                                height={540}
                                images={images}
                                showNavs={true}
                                onClick={handleOnClick}
                            />
                </div>
            </div>
    )
}