import * as React from 'react';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import { styled } from '@mui/system';
import MenuRoundedIcon from '@mui/icons-material/MenuRounded';
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';

const StyledStack = styled(Stack)({
    fontSize: '2vw',
    position: 'fixed',
    top: '1%',
    right: '1%',
    justifyContent: 'center',
    alignItems: 'center',
    alignContent: 'right',
    backgroundColor: 'transparent',
    zIndex: '1',
})

export default function MenuButton (){
    const [homeRoundedIconSize, setHomeRoundedIconSize] = React.useState('2vw');
    const [menuRoundedIconSize, setMenuRoundedIconSize] = React.useState('2vw');

    const onHomeIconMouseEnter = () => {
        setHomeRoundedIconSize('3vw');
    } 
    const onHomeIconMouseLeave = () => {
        setHomeRoundedIconSize('2vw');
    } 
    const onMenuIconMouseEnter = () => {
        setMenuRoundedIconSize('3vw');
    }
    const onMenuIconMouseLeave = () => {
        setMenuRoundedIconSize('2vw')
    }

    return (
        <div>
            <StyledStack
                direction='column'
                spacing={2}>
                <IconButton 
                    onMouseEnter={onHomeIconMouseEnter}
                    onMouseLeave={onHomeIconMouseLeave}
                    onClick={() => { window.location.href = '/'; }} 
                >
                    <HomeRoundedIcon 
                        sx={{color: '#de9c56', fontSize:{homeRoundedIconSize}}}
                    />
                </IconButton>
                <IconButton 
                    onMouseEnter={onMenuIconMouseEnter}
                    onMouseLeave={onMenuIconMouseLeave}
                    onClick={() => { window.location.href = '/menu'; }} 
                >
                    <MenuRoundedIcon 
                        sx={{color: '#de9c56', fontSize:{menuRoundedIconSize}}}
                    />
                </IconButton>
            </StyledStack>
      </div>
    )
  }
  