import * as React from 'react';
import {useState} from 'react';
import Subtitle from './Subtitle';
import Container from '@mui/material/Container';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ReactPlayer from 'react-player';
import {styled} from '@mui/system';
import { Typography } from '@mui/material';

const HomeContainer = styled(Container)({
    width: '100%',
    height: '100vh',
    backgroundColor: 'black',
    position: 'relative',
})

const HomeVideo = styled('video')({
    position: 'absolute',
    width: '95%',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)'
});

const HomeReactPlayer = styled(ReactPlayer)({
    position: 'absolute',
    width: '80vh',
    height: 'auto',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)'
})

const HomeTitle = styled(Typography)({
    fontFamily: 'Blacksword',
    fontSize: '5rem',
    position: 'absolute',
    top: '45%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    color: '#de9c56',
    fontWeight: 'bold',
    textShadow: ' -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black'
});


const ExpandMoreDiv = styled('a')({
    color: 'white',
    position: 'absolute',
    top: '90%',
    right: '0',
    fontSize: '1.2vw',
    cursor: 'pointer',
    display: 'flex',
    alignItems: 'center',
    zIndex: '3',
})

export default function Home(){
    const [expandMoreIconSize, setExpandMoreIconSize] = React.useState('3vw');
    const onMouseEnter = () => {
        setExpandMoreIconSize('3.5vw');
    }
    const onMouseLeave = () => {
        setExpandMoreIconSize('3vw');
    }

    return (
        <HomeContainer>
                <div>
                    {/* <HomeVideo 
                        autoPlay
                        loop={true}
                    >
                        <source
                            src='https://rougeonblue.ca/wp-content/uploads/2017/12/rob_home.mp4'
                            type='video/mp4'
                        />
                    </HomeVideo>  */}
                    <HomeReactPlayer
                        playing={true}
                        loop={true}
                        muted={true}
                        width='100%'
                        height='80vh'
                        url="./videos/logo.mp4"
                    />
                    <HomeTitle>
                        Porsche 718
                    </HomeTitle>
                    <Subtitle size='h6' />
                </div>      
                <ExpandMoreDiv
                    onMouseEnter={onMouseEnter}
                    onMouseLeave={onMouseLeave}
                    href='#IntroductionVideo'
                >
                    <ExpandMoreIcon style={{
                            width: expandMoreIconSize,
                            height: expandMoreIconSize
                        }} 
                    />
                    Scrollez pour explorer
                </ExpandMoreDiv>
        </HomeContainer>
    )
}


