import React, { Fragment, useState, useEffect } from 'react';
import ReactPlayer from 'react-player';
import { makeStyles } from '@material-ui/core/styles';
import useScroll from '../util/useScroll';
import Grow from '@mui/material/Grow';
import VizSensor from 'react-visibility-sensor';
import VolumeMuteIcon from '@mui/icons-material/VolumeMute';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import { IconButton } from '@mui/material';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: 'black',
    width: '100%',
    height: '100vh',
  },
  videoBox: {
    top: '20%',
    position: 'relative',
    width: '100%',
    height: '100vh',
    backgroundColor: 'black',
  },
  blackScreen: {
    position: 'absolute',
    top: '50%',
    transform: 'translate(-50%,-50%)',
    backgroundColor: 'black',
    height: '90vh',
  },
  videoContainer: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%,-50%)'
  },
  muteButtonContainer: {
    position: 'absolute',
    transform: 'translate(20%, 0%)',
    color: 'white',
    fontFamily: 'porschav3',
  }
}));

const slideBlackScreen = (blackScreenPercent:any, setBlackScreenPercent:any) => {
  if (blackScreenPercent === 100) return;
  setTimeout(() => {
    setBlackScreenPercent(blackScreenPercent + 2);
    slideBlackScreen(blackScreenPercent + 2, setBlackScreenPercent)
  }, 10)
}

export default function IntroductionVideo() {
  const classes = useStyles();
  const { x, y } = useScroll();
  const [loadingBox, setLoadingBox] = useState(false);
  const [blackScreenPercent, setBlackScreenPercent] = useState(0);
  const [soundMute, setSoundMute] = React.useState(true);

  useEffect(() => {
    const root:any= document.getElementsByClassName(classes.root)
    const offsetTop = root.length > 0 ? root[0].offsetTop : 1e9;
    const clientHeight = root.length > 0 ? root[0].clientHeight : 1e9;
    if (blackScreenPercent === 0 && offsetTop - clientHeight / 3 * 2 < y) {
      slideBlackScreen(blackScreenPercent, setBlackScreenPercent)
    }
  }, [y])

  const handleVolumeButtonClick = () => {
    setSoundMute(!soundMute);
  }

  return (
    <div className={classes.root}>
      <div className={classes.videoBox}>
        <div className={classes.videoContainer}>
          {/* <VizSensor onChange={(isVisible:boolean) => {setLoadingBox(isVisible)}}>
            <Grow in={loadingBox} timeout={1000}> */}
              <ReactPlayer
                playing={blackScreenPercent > 0}
                muted={soundMute}
                width='80vw'
                height='auto'
                url="./videos/718_vid.mp4"
              />
              <div className='muteButtonContainer'>
                <IconButton
                  size='large'
                  sx={{color: 'white'}}
                  onClick={handleVolumeButtonClick}>
                  {(soundMute) ? <VolumeMuteIcon>Muted</VolumeMuteIcon>: <VolumeUpIcon>Unmuted</VolumeUpIcon>}
                </IconButton>
              </div>
            {/* </Grow>
          </VizSensor> */}
        </div>
        {
          blackScreenPercent === 100 ? <Fragment /> : <div className={classes.blackScreen} style={{ left: `${50 + blackScreenPercent}%`, width: `${85 * (100 - blackScreenPercent) / 100}vw`, }} />
        }
      </div>
    </div>
  );
};
