import * as React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import VizSenseor from 'react-visibility-sensor';
import { Grow } from '@mui/material';
import { Grid } from '@mui/material';

const useStyles = makeStyles((theme) => ({
    root: {
        color: 'white',
        marginTop: '7vh',
        marginLeft: '11vw',
        fontSize: '1.5vw',
    },
    container: {
        display: 'flex',
        alignItem: 'center',
        justifyContent: 'center',
    },
    yearAfter: {
        width: '10vw',
        height: '1px',
        background: 'rgb(140, 140, 140)',
        right: '0vw',
        top: '50%',
    },
    year: {
        marginLeft: '0.5vw',
        fontFamily: 'Blacksword',
    },
    text: {
        fontSize: '1.5vw',
        fontFamily: 'porschev3'
    }
}));

type Props = {
    year: any,
    text: any,
};

export default function HistoryLine ({year, text}: Props) {
    const classes = useStyles();
    const id = `boxsterHistory${year}`
    const [load, setLoad] = React.useState(false);
    return (
        <div className={classes.root}>
            <VizSenseor onChange={(isVisible) => {if (isVisible && !load){setLoad(true)}}}>
                <Grow in={load} timeout={1000}>
                <Grid container spacing={1} id={id} className={classes.container}>
                    <Grid container item xs={2} spacing={3}>
                        <Grid xs={12}>
                        <div className={classes.year}>
                            {year}
                        </div>
                        </Grid>
                        <Grid xs={12}>
                            <div className={classes.yearAfter} />
                        </Grid>
                    </Grid>
                    <Grid container item xs={10} spacing={3}>
                        <div className={classes.text}>
                        {text}
                        </div>
                    </Grid>
                    </Grid>
                </Grow>
            </VizSenseor>
        </div>
    )
}