import * as React from 'react';
import { useCallback, useEffect } from 'react';
import Typography from '@mui/material/Typography';
import {styled} from '@mui/system';
import Fade from '@mui/material/Fade';


const intervalTime = 5000;

const StyledTypography = styled(Typography)({
    fontFamily: 'Blacksword',
    fontSize: '2.5rem',
    position: 'absolute',
    top: '57%',
    left: '50%',
    transform: 'translate( -50%, -50% )',
    color: '#de9c56',
    fontWeight: 'bold',
    textShadow: ' -0.5px 0 black, 0 0.5px black, 0.5px 0 black, 0 -0.5px black'
})

const Subtitle = (props: any) =>{
    const subtitles = [
        'Dare forward.',
        'Full Spectrum.',
        'Drive defines us.'
    ];

    const [subtitle, setSubtitle] = React.useState(subtitles[0])
    const [visible, setVisible] = React.useState(true);

    const rand = useCallback(() => {
        const index = Math.floor(Math.random() * subtitles.length);
        setVisible(!visible);
        setSubtitle(subtitles[index]);
    }, [visible]);
    
    useEffect(() => {
        const intervalID = setInterval(rand, intervalTime*2);
        return () => clearInterval(intervalID);
    }, [rand])

    return (
        <Fade 
            in={visible}
            timeout={{
                enter: intervalTime,
                exit: intervalTime*2,
            }}
        >
            <StyledTypography variant={props.size}>{subtitle}</StyledTypography>
        </Fade>
    );
}

export default Subtitle;