import * as React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import { Button, Stack, Box, Typography } from '@mui/material';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import WebsiteIcon from '@mui/icons-material/Language';
import {Grow} from '@mui/material';
import VizSensor from 'react-visibility-sensor';

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'relative',
        backgroundColor: 'black',
        justifyContent: 'center',
        alignItem: 'center',
        width: '100%',
        height: '10vw',
        display: 'flex',
        paddingBottom: '10vh',
    },
    container: {
        width: '80vw',
        height: '10vw',
        position: 'absolute',
    },
    snsBox: {
        width: '20vw',
        height: '7vw',
        position: 'relative',
        color: 'white',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    snsButton: {
        fontFamily: 'porschav3',
        fontSize: '1.3vw',
        color: 'white',
        position: 'relative',
    }
}));

export default function BottomLink (){
    const classes = useStyles();
    const [load, setLoad] = React.useState(false);
    
    return (
        <div className={classes.root}>
            <div className={classes.container}>
                <Stack direction="row">
                    <VizSensor onChange={(isVisible) => { if (isVisible && !load) {setLoad(true)}}}>
                        <Grow in={load} timeout={0}>
                            <Box className={classes.snsBox} sx={{border: 2, borderColor: 'white'}}>
                                <Button 
                                    className={classes.snsButton}
                                    variant="text" 
                                    startIcon={<LinkedInIcon 
                                        style={{fontSize: 60, color: 'white'}}
                                    />}
                                    href='https://www.linkedin.com/company/porsche-ag/'
                                >
                                    LinkedIn
                                </Button>
                            </Box>
                        </Grow>
                    </VizSensor>
                    <VizSensor onChange={(isVisible) => { if (isVisible && !load) {setLoad(true)}}}>
                        <Grow in={load} timeout={500}>
                            <Box className={classes.snsBox} sx={{border: 2, borderColor: 'white'}}>
                                <Button 
                                    className={classes.snsButton}
                                    variant="text" 
                                    startIcon={<InstagramIcon 
                                        style={{fontSize: 60, color: 'white'}}
                                    />}
                                    href='https://www.instagram.com/porsche.korea/'
                                >
                                    Instagram
                                </Button>
                            </Box>
                        </Grow>
                    </VizSensor>
                    <VizSensor onChange={(isVisible) => { if (isVisible && !load) {setLoad(true)}}}>
                        <Grow in={load} timeout={1000}>
                            <Box className={classes.snsBox} sx={{border: 2, borderColor: 'white'}}>
                                <Button 
                                    className={classes.snsButton}
                                    variant="text" 
                                    startIcon={<TwitterIcon 
                                        style={{fontSize: 60, color: 'white'}}
                                    />}
                                    href='https://twitter.com/Porsche'
                                >
                                    Twitter
                                </Button>
                            </Box>
                        </Grow>
                    </VizSensor>
                    <VizSensor onChange={(isVisible) => { if (isVisible && !load) {setLoad(true)}}}>
                        <Grow in={load} timeout={1500}>
                            <Box className={classes.snsBox} sx={{border: 2, borderColor: 'white'}}>
                                <Button 
                                    className={classes.snsButton}
                                    variant="text" 
                                    startIcon={<WebsiteIcon 
                                        style={{fontSize: 60, color: 'white'}}
                                    />}
                                    href='https://www.porsche.com/korea/ko/'
                                >
                                    Website
                                </Button>
                            </Box>
                        </Grow>
                    </VizSensor>
                </Stack>
            </div>       
        </div>
    )
}