import * as React from 'react';
import { styled } from '@mui/material';
import HistoryLine from './HistoryLine';

const RootDiv = styled('div')({
    backgroundColor: 'black',
    height: 'auto',
    position: 'relative',
    paddingBottom: '20vh'
})

const boxsterHistory = [
    [1993, 'The 1993 Porsche Boxster concept'],
    [1996, 'The first Boxster 986'],
    [2000, 'Boxster 986 got 2.7L, 3.2L engine'],
    [2003, 'Boxster 986 minor update'],
    [2005, 'The second Boxster 987'],
    [2007, 'Boxster 987.1 3.2L -> 3.4L'],
    [2009, 'Boxster 987.2 minor update'],
    [2012, 'The third Boxster 981'],
    [2014, 'Boxster 981 2.7L EoS'],
    [2015, 'Boxster 981 3.4L -> 3.8L'],
    [2016, 'The fourth Boxster 718'],
    [2019, 'Boxster 718 GTS EoS'],
    [2020, 'New Boxster 718 GTS 4.0'],
    [2022, 'New Boxster 718 GT4 RS']
]

export default function History() {
    return (
        <RootDiv>
            <div>
            {boxsterHistory.map(([year, text]) => (
                <HistoryLine
                    year={year}
                    text={text} />
                ))}
            </div>
        </RootDiv>
    )
}