import React from 'react';
import ReactDOM from 'react-dom';
import FloatingActionButton from './components/Fab/FloatingActionButton';
import Home from './components/Home/Home';
import LoaderAnimation from './components/Loader/LoaderAnimation';
import BottomLink from './components/Main/BottomLink';
import BuildYourBoxster from './components/Main/BuildYourBoxster';
import History from './components/Main/History';
import IntroductionVideo from './components/Main/IntroductionVideo';
import PictureWithDetail from './components/Main/PictureWithDetail';
import MenuButton from './components/Menu/MenuButton';
import './index.css';

ReactDOM.render(  
  <React.StrictMode>
    <div style={{background: 'black'}}>
      <div style={{position: 'absolute', width: '100%'}}>
        <Home />
        <IntroductionVideo />
        <PictureWithDetail />
        <History/>
        <BuildYourBoxster />
        <BottomLink />
      </div>
        <MenuButton />
        <FloatingActionButton />
        <LoaderAnimation />
    </div>

  </React.StrictMode>,
  document.getElementById('root')
);