import * as React from 'react';
import ReactPlayer from 'react-player';
import {makeStyles} from '@material-ui/core/styles';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import useScroll from '../util/useScroll';

import Fade from '@mui/material/Fade';
import Grow from '@mui/material/Grow';
import Collapse from '@mui/material/Collapse';
import Slide from '@mui/material/Slide';
import VizSensor from 'react-visibility-sensor';

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: 'black',
        justifyContent: 'center',
        alignItem: 'center',
        width: '100%',
        height: '150vh',
        display: 'flex',
    },
    detailBox: {
        top: '20%',
        position: 'relative',
        height: '100vh',
        width: '80%',
        backgroundColor: 'black',
    },
    upperLeftBox: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: 'auto',
        height: '50vh',
        color: 'white'
    },
    bottomLeftBox:{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: 'auto',
        height: '50vh',
    },
    upperRightBox: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: 'auto',
        height: '50vh',
    },
    bottomRightBox:{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: 'auto',
        height: '50vh',
        color: 'white',
    },
}))

const test_text = "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin sit amet urna sed augue vestibulum ornare. Quisque et condimentum massa. Suspendisse placerat euismod ipsum, eget luctus nisl accumsan eu. Aenean venenatis, felis sit amet faucibus porttitor, turpis orci sollicitudin purus, vitae dignissim ex nibh quis felis. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vestibulum nibh nulla, hendrerit id nunc sollicitudin, hendrerit ornare lacus. "

export default function PictureWithDetail(){
    const classes = useStyles();

    const [upperLeftLoad, setUpperLeftLoad] = React.useState(false);
    const [bottomLeftLoad, setBottomLeftLoad] = React.useState(false);
    const [upperRightLoad, setUpperRightLoad] = React.useState(false);
    const [bottomRightLoad, setBottomRightLoad] = React.useState(false);


    return (
        <div className={classes.root}>
                <div className={classes.detailBox}>
                    <Grid container spacing={3}>
                        <VizSensor onChange={(isVisible) => { if (isVisible && !upperLeftLoad) {setUpperLeftLoad(true)}}}>
                        <Grow in={upperLeftLoad} timeout={1000}>
                            <Grid item xs={6}>
                                <Box border={0} className={classes.upperLeftBox}>
                                    <Typography>
                                        {test_text}
                                        {test_text}
                                    </Typography>
                                </Box>
                            </Grid>
                        </Grow>
                        </VizSensor>


                        <VizSensor onChange={(isVisible) => { if (isVisible && !bottomLeftLoad) {setBottomLeftLoad(true)}}}>
                        <Fade in={bottomLeftLoad} timeout={3000}>
                            <Grid item xs={6}>
                                <Box border={0} className={classes.bottomLeftBox}>
                                    <img
                                        style={{borderRadius: '2%'}}
                                        width='100%'
                                        aria-label='718_cayman_gt4_rs' 
                                        src='img/718_GT4_RS_1.jpg' />
                                </Box>
                            </Grid>
                        </Fade>
                        </VizSensor>

                        <VizSensor onChange={(isVisible) => { if (isVisible && !upperRightLoad) {setUpperRightLoad(true)}}}>
                        <Fade in={upperRightLoad} timeout={2000}>
                            <Grid item xs={6}>
                                <Box border={0} className={classes.upperRightBox}>
                                    <img
                                        style={{borderRadius: '2%'}}
                                        aria-label='718_cayman_gt4_rs2' 
                                        width='100%'
                                        height='auto'
                                        src='img/718_GT4_RS_2.jpg' />
                                </Box>
                            </Grid>
                        </Fade>
                        </VizSensor>

                        <VizSensor onChange={(isVisible) => { if (isVisible && !bottomRightLoad) {setBottomRightLoad(true)}}}>
                        <Grow in={bottomRightLoad} timeout={2000}>
                            <Grid item xs={6}>
                                <Box border={0} className={classes.bottomRightBox}>
                                    <Typography>
                                        {test_text}
                                        {test_text}
                                    </Typography>
                                </Box>
                            </Grid> 
                        </Grow>
                        </VizSensor>
                    </Grid>
                </div>
        </div>
    )
}