import * as React from 'react';
import { Fragment, useState, useEffect, useMemo } from 'react';
import {fontSize, positions, styled} from '@mui/system';
import { Typography } from '@mui/material';
import { Fade } from '@mui/material';
import Countdown from 'react-countdown';
import zIndex from '@mui/material/styles/zIndex';

const RootDiv = styled('div')({
    zIndex: 3,
    backgroundColor: 'black',
    position: 'absolute',
    width: '100%',
    height: '100vh',
});

const LoaderTitle = styled(Typography)({
    fontFamily: 'Blacksword',
    color: 'white',
    fontSize: '4.5vw',
    alignContent: 'center',
    fontWeight: 'bold',
    position: 'absolute',
    textAlign: 'center',
    top: '40%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
});

const LoaderSubtitle = styled(Typography)({
    fontFamily: 'Blacksword',
    color: 'white',
    fontSize: '2.5vw',
    alignContent: 'center',
    position: 'absolute',
    textAlign: 'center',
    top: '60%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
});


export default function LoaderAnimation(){
    const year = new Date().getFullYear();
    const [counter, setCounter] : [number, Function] = React.useState(year);
    const [transition, setTransition] = React.useState(true);
    
    const changeTransition = () => {
        setTransition(false);
    }

    useEffect(() => {
        setTimeout(changeTransition, 3000);
    })

    useMemo(() => {
        const tick = setTimeout(() => {
            setCounter((counter > 1996)? counter - 1 : 1996);
        }, 300 - (year - counter) * 40);
       return () => {
           clearTimeout(tick);
        };
    }, [counter, year]);


    const [opac, setOpac] = React.useState(1);

    const changeOpacity = () => {
        setOpac(0);
    }

    useEffect(() => {
        setTimeout(changeOpacity, 3300);
        console.log(opac);
    })

    return (
        <div style={{opacity: `${opac}`, width: '0px', height: '0px'}}>
            <RootDiv>
                    <div>
                        <Fade in={transition}>
                            <LoaderTitle >
                                Porsche Boxster
                            </LoaderTitle>
                        </Fade>
                        <Fade in={transition}>
                            <LoaderSubtitle>
                                Since {counter}
                            </LoaderSubtitle>
                        </Fade>
                    </div>
            </RootDiv>
        </div>
    )
}